	module.exports = {"data":{"wordpressPost":{"title":"Editors picks","content":"<h1>BMJ Open: August Editor&#8217;s Picks</h1>\n<p><img class=\"alignnone\" title=\"Image source: Wikimedia Commons; Author: DFID - UK Department for International Development\" src=\"https://upload.wikimedia.org/wikipedia/commons/thumb/7/7e/The_harrowing_tales_of_Female_Genital_Mutilation-Cutting_%2812344890033%29.jpg/800px-The_harrowing_tales_of_Female_Genital_Mutilation-Cutting_%2812344890033%29.jpg\" alt=\"File:The harrowing tales of Female Genital Mutilation-Cutting (12344890033).jpg\" width=\"340\" height=\"226\" /></p>\n<p><em>Each month the editorial team at BMJ Open will be selecting highlights from our recently published articles. We hope you enjoy our August highlights. You can access all the material published in August <a href=\"http://bmjopen.bmj.com/content/7/8?current-issue=y\">here</a>. </em></p>\n<h3>When are interventions justified? Attitudes towards female genital cutting</h3>\n<p>Sometimes baseline data can reveal that implementing an intervention is unnecessary and, perhaps, unethical. In <a href=\"http://bmjopen.bmj.com/content/7/8/e017506\">a study from Wahlburg and colleagues looking at attitudes to female genital cutting (FGC) in Somali migrants in Sweden</a>, baseline data revealed that the majority&#8211;including those newly arrived&#8211;were opposed to all forms of FGC with increased opposition over time after migration. The researchers determined that it would be unethical to carry out their planned intervention, designed to change attitudes to FGC, given that the change was already occurring.</p>\n<h3>Clinical decision support systems: Are they trustworthy?</h3>\n<p>Using the best evidence to inform clinical decisions remains a challenge. Rapid response clinical decision support systems represent an attractive option, but their trustworthiness is unknown. <a href=\"http://bmjopen.bmj.com/content/7/8/e016113\">Izcovich and colleagues use PubMed clinical queries and Epistemonikos based on the GRADE approach to answer 100 clinical questions</a> and compared it to a gold standard. The approach proved feasible and provided appropriate guidance for most questions.</p>\n<h3>What contributes to psychological distress in doctors?</h3>\n<p>Doctors can experience high levels of psychological distress&#8211;a concept that includes stress, burnout, depression, and anxiety. Using a qualitative study design, <a href=\"http://bmjopen.bmj.com/content/7/8/e015890\">Tallentire and colleagues<em> </em>explore the specific workplace stressors that contribute to psychological distress</a>. Key themes include work overload, long hours, uncertainty about the role, and relationships with colleagues.</p>\n<h3 role=\"button\">Telehealth and patient satisfaction</h3>\n<div>\n<div class=\"yj6qo ajU\">\n<p role=\"button\">Telehealth incorporates technology into diverse modes of delivery such as videoconferencing, mobile applications, and secure messaging. However, when using technology, it is important to listen to patients &#8220;to help guard against the implementation of technology merely for its convenience or shiny appeal&#8221;. By conducting a systematic review, <a href=\"http://bmjopen.bmj.com/content/7/8/e016242\">Kruse and colleagues investigate what patients believe to be the most satisfying modes of telehealth</a>.</p>\n</div>\n</div>\n<h3 role=\"button\">NHS Health Check programme: What works, and what can be improved?</h3>\n<p>NHS Health Checks are a public health service in the UK designed to identify early signs of stroke, kidney disease, heart disease, type 2 diabetes, or dementia in individuals aged 40-74. In a systematic review and qualitative synthesis of the literature, <a href=\"http://bmjopen.bmj.com/content/7/8/e017169\">Usher-Smith and colleagues investigate patients&#8217; experiences of the service</a>, finding that, while patients are broadly supportive, there are key areas with room for improvement; for example, providing more proactive support for lifestyle change.</p>\n<div></div>\n<p><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby for BMJ blog sites"}}},"pathContext":{"id":"128dac26-9d6a-54ca-b65c-3408f5b072e0"}}

